import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    renderRecipes: false,
    showRecipe: false,
    activeRecipe: "String",
    saveRecipe: false,
  },
  mutations: {
    setRenderRecipes(state, payload) {
      state.renderRecipes = payload;
    },
    setShowRecipe(state, payload) {
      state.showRecipe = payload;
    },
    setSaveRecipe(state, payload) {
      state.saveRecipe = payload;
      console.log(state.saveRecipe);
    },
    setActiveRecipe(state, payload) {
      state.activeRecipe = payload;
    },
  },
  getters: {
    getRenderRecipes: (state) => state.renderRecipes,
    getShowRecipe: (state) => state.showRecipe,
    getActiveRecipe: (state) => state.activeRecipe,
    getSaveRecipe: (state) => state.saveRecipe,
  },
});
