<template>
  <div>
    <v-card-text>
      <v-text-field class="my-3" label="Recipe Name" v-model="value.name">
      </v-text-field>
      <v-textarea height="100" label="Description" v-model="value.description">
      </v-textarea>
      <div class="my-2"></div>
      <v-row>
        <v-col cols="4">
          <h2 class="mb-4">Ingredients</h2>
          <div
            v-for="(ingredient, index) in value.recipeIngredient"
            :key="generateKey('ingredient', index)"
          >
            <v-row align="center">
              <v-btn
                fab
                x-small
                color="red"
                class="mr-2"
                elevation="1"
                @click="removeIngredient(index)"
              >
                <v-icon color="white">mdi-delete</v-icon>
              </v-btn>
              <v-text-field
                label="Ingredient"
                v-model="value.recipeIngredient[index]"
              ></v-text-field>
            </v-row>
          </div>
          <v-btn
            color="primary"
            fab
            dark
            small
            @click="addIngredient"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>

          <h2 class="mt-6">Categories</h2>
          <v-combobox
            dense
            multiple
            chips
            item-color="primary"
            deletable-chips
            v-model="value.categories"
          >
            <template v-slot:selection="data">
              <v-chip :input-value="data.selected" close color="primary" dark>
                {{ data.item }}
              </v-chip>
            </template>
          </v-combobox>

          <h2 class="mt-4">Tags</h2>
          <v-combobox
            dense
            multiple
            chips
            deletable-chips
            v-model="value.tags"
          >
            <template v-slot:selection="data">
              <v-chip :input-value="data.selected" close color="primary" dark>
                {{ data.item }}
              </v-chip>
            </template>
          </v-combobox>

          <h2 class="my-4">Notes</h2>
          <v-card
            class="mt-1"
            v-for="(note, index) in value.notes"
            :key="generateKey('note', index)"
          >
            <v-card-text>
              <v-row align="center">
                <v-btn
                  fab
                  x-small
                  color="red"
                  class="mr-2"
                  elevation="1"
                  @click="removeNote(index)"
                >
                  <v-icon color="white">mdi-delete</v-icon>
                </v-btn>
                <v-text-field
                  label="Title"
                  v-model="value.notes[index]['title']"
                ></v-text-field>
              </v-row>

              <v-textarea
                label="Note"
                v-model="value.notes[index]['text']"
              >
              </v-textarea>
            </v-card-text>
          </v-card>
          <v-btn class="mt-1" color="primary" fab dark small @click="addNote">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-col>

        <v-divider :vertical="true"></v-divider>

        <v-col>
          <h2 class="mb-4">Instructions</h2>
          <div v-for="(step, index) in value.recipeInstructions" :key="index">
            <v-hover v-slot="{ hover }">
              <v-card
                class="ma-1"
                :class="[{ 'on-hover': hover }]"
                :elevation="hover ? 12 : 2"
              >
                <v-card-title>
                  <v-btn
                    fab
                    x-small
                    color="red"
                    class="mr-2"
                    elevation="1"
                    @click="removeStep(index)"
                  >
                    <v-icon color="white">mdi-delete</v-icon> </v-btn
                  >Step: {{ index + 1 }}</v-card-title
                >
                <v-card-text>
                  <v-textarea
                    dense
                    v-model="value.recipeInstructions[index]['text']"
                    :key="generateKey('instructions', index)"
                  ></v-textarea>
                </v-card-text>
              </v-card>
            </v-hover>
          </div>
          <v-btn color="primary" fab dark small @click="addStep">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-text>
  </div>
</template>

<script>
export default {
  props: {
    value: Object,
  },
  data() {
    return {
      content: this.value,
      disabledSteps: [],
      description: String,
      ingredients: Array,
      instructions: Array,
      categories: Array,
      tags: Array,
    };
  },
  methods: {
    toggleDisabled(stepIndex) {
      if (this.disabledSteps.includes(stepIndex)) {
        let index = this.disabledSteps.indexOf(stepIndex);
        if (index !== -1) {
          this.disabledSteps.splice(index, 1);
        }
      } else {
        this.disabledSteps.push(stepIndex);
      }
    },
    isDisabled(stepIndex) {
      if (this.disabledSteps.includes(stepIndex)) {
        return "disabled-card";
      } else {
        return;
      }
    },
    generateKey(item, index) {
      const uniqueKey = `${item}-${index}`;
      return uniqueKey;
    },
    deleteRecipe() {
      this.$emit("delete");
    },

    addIngredient() {
      let list = this.value.recipeIngredient;
      list.push("");
    },

    removeIngredient(index) {
      this.value.recipeIngredient.splice(index, 1);
    },

    addStep() {
      let list = this.value.recipeInstructions;
      list.push({ text: "" });
    },
    removeStep(index) {
      this.value.recipeInstructions.splice(index, 1);
    },

    addNote() {
      let list = this.value.notes;
      list.push({ text: "" });
    },
    removeNote(index) {
      this.value.notes.splice(index, 1);
    },
  },
};
</script>

<style>
.disabled-card {
  opacity: 50%;
}
</style>