<template>
  <v-card>
    <v-img max-height="400" :src="getImage(recipeDetails.image)"> </v-img>
    <v-toolbar class="card-btn" flat height="0" extension-height="0">
      <template v-slot:extension>
        <v-col></v-col>
        <div v-if="showIcons">
          <v-btn class="mr-2" fab dark small color="red" @click="deleteRecipe">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-btn class="mr-2" fab dark small color="green" @click="saveRecipe">
            <v-icon>mdi-content-save</v-icon>
          </v-btn>
          <v-btn
            class="mr-5"
            fab
            dark
            small
            color="secondary"
            @click="jsonEditor = true"
          >
            <v-icon>mdi-code-braces</v-icon>
          </v-btn>
        </div>
        <v-btn color="primary" fab dark small @click="showForm">
          <v-icon>mdi-square-edit-outline</v-icon>
        </v-btn>
      </template>
    </v-toolbar>

    <ViewRecipe
      v-if="!form"
      :name="recipeDetails.name"
      :ingredients="recipeDetails.recipeIngredient"
      :description="recipeDetails.description"
      :instructions="recipeDetails.recipeInstructions"
      :tags="recipeDetails.tags"
      :categories="recipeDetails.categories"
      :notes="recipeDetails.notes"
    />
    <VJsoneditor
      class="mt-10"
      v-else-if="showJsonEditor"
      v-model="recipeDetails"
      height="1500px"
      :options="jsonEditorOptions"
    />
    <EditRecipe v-else v-model="recipeDetails" />
  </v-card>
</template>

<script>
import axios from "axios";
import VJsoneditor from "v-jsoneditor";
import ViewRecipe from "./RecipeEditor/ViewRecipe";
import EditRecipe from "./RecipeEditor/EditRecipe";
const GET_URL = `/api/recipe/`;

export default {
  components: {
    VJsoneditor,
    ViewRecipe,
    EditRecipe,
  },
  data() {
    return {
      UrlName: this.$route.params.recipe,
      form: false,
      jsonEditor: false,
      jsonEditorOptions: {
        mode: "code",
        search: false,
        mainMenuBar: false,
      },
      // Recipe Details //
      recipeDetails: {
        name: "",
        description: "",
        image: "",
        recipeYield: "",
        recipeIngredient: [],
        recipeInstructions: [],
        slug: "",
        filePath: "",
        url: "",
        tags: [],
        categories: [],
        dateAdded: "",
        notes: [],
      },
    };
  },
  mounted() {
    this.getRecipeDetails();
  },

  computed: {
    showRecipe() {
      return this.$store.getters.getShowRecipe;
    },
    showIcons() {
      return this.form;
    },
    showJsonEditor() {
      if ((this.form === true) & (this.jsonEditor === true)) {
        return true;
      } else {
        return false;
      }
    },
  },

  methods: {
    async getRecipeDetails() {
      const recipeURL = GET_URL + this.UrlName;
      let response = await axios.get(recipeURL);
      this.recipeDetails = response.data;
      this.form = false;
    },
    getImage(image) {
      if (image != false) {
        let image_url = `/api/recipe/image/${image}`;
        return image_url;
      }
    },
    close() {
      this.$store.commit("setShowRecipe", false);
    },
    deleteRecipe() {
      let delete_url = `/api/recipe/${this.recipeDetails.slug}/delete`;
      axios.delete(delete_url);
      this.form = false;
      this.$router.push(`/`);
    },
    saveRecipe() {
      const recipeSlug = this.recipeDetails.slug;
      const saveURL = `/api/recipe/${recipeSlug}/update`;

      axios.post(saveURL, this.recipeDetails);
      this.$store.commit("setSaveRecipe", false);
      this.form = false;
    },
    showForm() {
      this.form = true;
      this.jsonEditor = false;
    },
  },
};
</script>

<style>
.card-btn {
  margin-top: -10px;
}
.disabled-card {
  opacity: 50%;
}
</style>