<template>
  <v-row>
    <v-col cols="3" v-for="recipe in recipes" :key="recipe.name">
      <v-hover v-slot="{ hover }" :open-delay="50">
        <v-card
          class="card-container"
          :class="{ 'on-hover': hover }"
          :elevation="hover ? 12 : 2"
          height="400"
          @click="moreInfo(recipe.slug)"
        >
          <v-img height="200" :src="getImage(recipe.image)"></v-img>
          <v-card-title>{{ recipe.name }}</v-card-title>
          <v-card-text>
            {{ recipe.description | truncate(125, "...") }}
          </v-card-text>
        </v-card>
      </v-hover>
    </v-col>
  </v-row>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    recipes: [],
  }),
  mounted() {
    this.getRecipes();
  },
  computed: {
    reRender() {
      return this.$store.getters.getRenderRecipes;
    },
  },
  watch: {
    reRender: function () {
      this.getRecipes();
    },
  },
  methods: {
    async getRecipes() {
      let values = {
        keys: ["name", "slug", "image", "description", "dateAdded"],
      };
      let response = await axios.post(`/api/all-recipes`, values);
      let recipe = response.data;
      recipe.sort((a, b) => (a.dateAdded > b.dateAdded ? -1 : 1));
      this.recipes = recipe;
      this.$store.commit("setRenderRecipes", false);
    },
    getImage(image) {
      let image_url = `/api/recipe/image/${image}`;
      return image_url;
    },
    moreInfo(recipeSlug) {
      this.$router.push(`recipe/${recipeSlug}`);
    },
  },
};
</script>

