<template>
  <div>
    <v-card-title class="headline">
      {{ name }}
    </v-card-title>
    <v-card-text>
      {{ description }}
      <div class="my-2"></div>
      <v-row>
        <v-col cols="4">
          <h2 class="mb-4">Ingredients</h2>
          <div v-for="ingredient in ingredients" :key="ingredient">
            <v-checkbox hide-details class="ingredients" :label="ingredient">
            </v-checkbox>
          </div>

          <div v-if="categories[0]">
            <h2 class="mt-4">Categories</h2>
            <v-chip
              class="ma-1"
              color="primary"
              dark
              v-for="category in categories"
              :key="category"
            >
              {{ category }}
            </v-chip>
          </div>

          <div v-if="tags[0]">
            <h2 class="mt-4">Tags</h2>
            <v-chip
              class="ma-1"
              color="primary"
              dark
              v-for="tag in tags"
              :key="tag"
            >
              {{ tag }}
            </v-chip>
          </div>

          <h2 v-if="notes[0]" class="my-4">Notes</h2>
          <v-card class="mt-1" v-for="(note, index) in notes" :key="index">
            <v-card-title> {{ note.title }}</v-card-title>
            <v-card-text>
              {{ note.text }}
            </v-card-text>
          </v-card>
        </v-col>

        <v-divider :vertical="true"></v-divider>

        <v-col>
          <h2 class="mb-4">Instructions</h2>
          <div v-for="(step, index) in instructions" :key="step.text">
            <v-hover v-slot="{ hover }">
              <v-card
                class="ma-1"
                :class="[{ 'on-hover': hover }, isDisabled(index)]"
                :elevation="hover ? 12 : 2"
                @click="toggleDisabled(index)"
              >
                <v-card-title>Step: {{ index + 1 }}</v-card-title>
                <v-card-text>{{ step.text }}</v-card-text>
              </v-card>
            </v-hover>
          </div>
        </v-col>
      </v-row>
    </v-card-text>
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    description: String,
    ingredients: Array,
    instructions: Array,
    categories: Array,
    tags: Array,
    notes: Array,
  },
  data() {
    return {
      disabledSteps: [],
    };
  },
  methods: {
    toggleDisabled(stepIndex) {
      if (this.disabledSteps.includes(stepIndex)) {
        let index = this.disabledSteps.indexOf(stepIndex);
        if (index !== -1) {
          this.disabledSteps.splice(index, 1);
        }
      } else {
        this.disabledSteps.push(stepIndex);
      }
    },
    isDisabled(stepIndex) {
      if (this.disabledSteps.includes(stepIndex)) {
        return "disabled-card";
      } else {
        return;
      }
    },
  },
};
</script>

