<template>
  <v-card>
    <v-img v-if="image" height="400" :src="image"> </v-img>
    <br v-else />

    <ButtonRow
      @json="jsonEditor = true"
      @editor="jsonEditor = false"
      @save="createRecipe"
    />

    <v-row>
      <v-col cols="3"></v-col>
      <v-col>
        <v-file-input
          v-model="fileObject"
          label="Image File"
          truncate-length="30"
          @change="onFileChange"
        ></v-file-input>
      </v-col>
      <v-col cols="3"></v-col>
    </v-row>

    <VJsoneditor
      v-if="jsonEditor"
      v-model="recipeDetails"
      height="1500px"
      :options="jsonEditorOptions"
    />

    <EditRecipe v-else v-model="recipeDetails" />
  </v-card>
</template>

<script>
import axios from "axios";
const CREATE_URL = `/api/recipe/create/`;

import EditRecipe from "./RecipeEditor/EditRecipe";
import VJsoneditor from "v-jsoneditor";
import ButtonRow from "./UI/ButtonRow";
export default {
  components: {
    VJsoneditor,
    EditRecipe,
    ButtonRow,
  },
  data() {
    return {
      fileObject: null,
      image: null,
      jsonEditor: false,
      jsonEditorOptions: {
        mode: "code",
        search: false,
        mainMenuBar: false,
      },
      recipeDetails: {
        name: "",
        description: "",
        image: "",
        recipeYield: "",
        recipeIngredient: [],
        recipeInstructions: [],
        slug: "",
        filePath: "",
        tags: [],
        categories: [],
        dateAdded: "",
        notes: [],
      },
    };
  },

  watch: {
    image() {
      console.log(this.image);
    },
  },

  methods: {
    onFileChange() {
      this.image = URL.createObjectURL(this.fileObject);
    },
    async createRecipe() {
      console.log(this.recipeDetails);
      let response = await axios.post(CREATE_URL, this.recipeDetails);
      this.$router.push(`/recipe/${response.data}`);
      console.log(response);
    },
  },
};
</script>

<style>
.img-input {
  position: absolute;
  bottom: 0;
}
</style>